import { Component, OnInit, Input } from '@angular/core';
import { NzDrawerRef } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { AbsensiJadwalPegawaiTerjadwalService, AbsensiPilotAircraftService, AbsensiPilotRonService, AbsensiPilotJadwalDetailService } from 'src/app/sdk/hr/services';
import { Router } from '@angular/router';
import { PayrollDerazonaPegawaiDerazonaService } from 'src/app/sdk/payroll/services';
import { Pegawai_derazona } from 'src/app/sdk/payroll/models';
import { PayrollDerazonaPegawaiRonPayService } from 'src/app/sdk/payroll/services/payroll-derazona-pegawai-ron-pay.service';
import { Pegawai_ron_pay } from 'src/app/sdk/payroll/models/pegawai-_ron-_pay';

@Component({
    selector: 'app-verifikasi',
    templateUrl: './verifikasi.component.html',
    styleUrls: ['./verifikasi.component.scss']
})
export class VerifikasiComponent implements OnInit {

    @Input() id_jadwal_pegawai = '';

    constructor(
        private drawerRef: NzDrawerRef<boolean>,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private absensiJadwalPegawaiTerjadwalService: AbsensiJadwalPegawaiTerjadwalService,
        private payrollDerazonaPegawaiDerazonaService: PayrollDerazonaPegawaiDerazonaService,
        private payrollDerazonaPegawaiRonPayService: PayrollDerazonaPegawaiRonPayService,
        private router: Router,
        private absensiPilotRonService: AbsensiPilotRonService,
    ) { }

    ngOnInit() {
        console.log(this.id_jadwal_pegawai);
        this.getJadwalKerja()
    }

    listData: any = {}
    getJadwalKerja() {
        // this.spinner.show()
        let query = {
            id_jadwal_pegawai: this.id_jadwal_pegawai
        }

        this.absensiJadwalPegawaiTerjadwalService.getAbsensiJadwalPegawaiTerjadwals(JSON.stringify(query)).subscribe(
            data => {
                let out = [];

                data.data.forEach(item => {
                    this.listData = item
                    this.id_pegawai = item.id_pegawai;
                    this.getRonGroupPegawai(item.id_pegawai);
                    this.getNamaRon(item['id_ron_kode'])

                })

            }
        )
    }

    id_pegawai = null
    pengaturan: Pegawai_derazona = {}
    pesan_text = ''
    getRonGroupPegawai(id_pegawai) {
        this.pesan_text = ''
        this.spinner.show()
        this.payrollDerazonaPegawaiDerazonaService.getPayrollDerazonaPegawaiDerazonasId(id_pegawai).subscribe(
            data => {
                this.spinner.hide()
                this.pengaturan = data.data;
                if (this.pengaturan.id_ron_group) {
                    this.preeCreate()
                }
                else {
                    this.pesan_text = 'Belum ada setingan RON GROUP'
                    this.pesan.pesanWarningForm(this.pesan_text)

                }

            }
        )
    }

    getNamaRon(id_ron) {
        this.absensiPilotRonService.getAbsensiPilotRonsId(id_ron).subscribe(
            data => {
                this.listData['ron'] = data.data;
            }
        )
    }

    close(): void {
        this.drawerRef.close(true);
    }

    ron_pay: Pegawai_ron_pay = {}
    is_loading_pree = true
    preeCreate() {
        this.is_loading_pree = true;
        let param: Pegawai_ron_pay = {
            id_jadwal_pegawai: this.id_jadwal_pegawai,
            id_pegawai: this.id_pegawai,
            tanggal: this.listData.jam_masuk,
            id_ron: this.listData.id_ron_kode,
            id_ron_group: this.pengaturan.id_ron_group,
            verifikasi: 0
        }
        this.payrollDerazonaPegawaiRonPayService.postPayrollDerazonaPegawaiRonPays(param).subscribe(
            data => {
                this.ron_pay = data.data
                this.is_loading_pree = false;

                if (this.listData.extra_duty != 'ya') {
                    this.ron_pay.extra_duty = 0;
                    this.ron_pay.o_1 = 0;
                }
                else {
                    this.ron_pay.autty_allow = 0;

                    if (this.listData.extra_duty_vanue != 'ya') {
                        this.ron_pay.extra_duty = 0;
                    }
                    else {
                        this.ron_pay.o_1 = 0;
                    }
                }

            }
        )
    }

    simpan() {
        this.spinner.show();
        this.ron_pay.verifikasi = 1;
        this.payrollDerazonaPegawaiRonPayService.putPayrollDerazonaPegawaiRonPaysId({
            id: this.ron_pay.id_pegawai_ron_pay,
            body: this.ron_pay
        }).subscribe(
            data => {
                this.spinner.hide()
                this.close()
            }
        )

    }

}
