import { Component, OnInit, Input } from '@angular/core';
import { NzDrawerRef } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { AbsensiJadwalPegawaiTerjadwalService, AbsensiPilotAircraftService, AbsensiPilotRonService, AbsensiPilotJadwalDetailService } from 'src/app/sdk/hr/services';
import { Router } from '@angular/router';
import { Jadwal_pegawai_terjadwal } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {


    @Input() tahun = '';
    @Input() bulan = '';
    @Input() id_jadwal_pegawai = '';

    constructor(
        private drawerRef: NzDrawerRef<boolean>,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private absensiJadwalPegawaiTerjadwalService: AbsensiJadwalPegawaiTerjadwalService,
        private router: Router,
        private absensiPilotAircraftService: AbsensiPilotAircraftService,
        private absensiPilotRonService: AbsensiPilotRonService,
        private absensiPilotJadwalDetailService: AbsensiPilotJadwalDetailService,
    ) { }

    ngOnInit() {
        console.log(this.id_jadwal_pegawai);
        this.getJadwalKerja()
        this.getRon()
        this.getAircraft()
    }
    //Derazona khusus --------------------------------------

    ron = [];
    getRon() {
        this.absensiPilotRonService.getAbsensiPilotRons().subscribe(
            data => {
                this.ron = data.data;
            }
        )
    }

    aircraft = []
    getAircraft() {
        this.absensiPilotAircraftService.getAbsensiPilotAircrafts().subscribe(
            data => {
                this.aircraft = data.data;
            }
        )
    }



    //Derazona khusus --------------------------------------

    listJadwakType: any[] = []


    close(): void {
        this.drawerRef.close(true);
    }

    numberToHari = ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu'];
    getJadwalKerja() {
        // this.spinner.show()
        let query = {
            id_jadwal_pegawai: this.id_jadwal_pegawai
        }

        this.absensiJadwalPegawaiTerjadwalService.getAbsensiJadwalPegawaiTerjadwals(JSON.stringify(query)).subscribe(
            data => {
                let out = [];
                data.data.forEach(item => {

                    let param: any = item;
                    param['hari'] = this.numberToHari[parseInt(item['hari']) - 1];
                    param.jam_masuk = new Date(item.jam_masuk);

                    if (param.status_jadwal == 'ya') {
                        param.status_jadwal = true;
                    } else (
                        param.status_jadwal = false
                    )

                    if (param.extra_duty == 'ya') {
                        param.extra_duty = true;
                    } else (
                        param.extra_duty = false
                    )

                    out.push(param);
                    console.log(param)

                })
                this.listJadwakType = out;
                this.width = (200 + (out.length * 200)) + 'px';
                // this.spinner.hide()
            }
        )
    }

    getJadwalPilot() {

    }

    listWarna = [{ warna: 'WHITE	', color: '#fff' },
    { warna: 'SILVER	', color: '#C0C0C0' },
    { warna: 'GRAY	', color: '#808080' },
    { warna: 'BLACK	', color: '#000000' },
    { warna: 'RED	', color: '#FF0000' },
    { warna: 'MAROON	', color: '#800000' },
    { warna: 'YELLOW	', color: '#FFFF00' },
    { warna: 'OLIVE	', color: '#808000' },
    { warna: 'LIME	', color: '#00FF00' },
    { warna: 'GREEN	', color: '#008000' },
    { warna: 'AQUA	', color: '#00FFFF' },
    { warna: 'TEAL	', color: '#008080' },
    { warna: 'BLUE	', color: '#0000FF' },
    { warna: 'NAVY	', color: '#000080' },
    { warna: 'FUCHSIA	', color: '#FF00FF' },
    { warna: 'PURPLE	', color: '#800080' }];

    listParameter = [
        { key: 'status_jadwal', val: 'Status' },
        { key: 'extra_duty', val: 'Extra Duty' },
        { key: 'extra_duty_vanue', val: 'Extra Duty Vanue' },
        { key: 'jam_masuk', val: 'Masuk' },
        { key: 'lama_kerja', val: 'Kerja' },
        { key: 'lama_istirahat', val: 'Istirahat' },
        { key: 'toleransi_telat', val: 'Toleransi T' },
        { key: 'toleransi_cabut', val: 'Toleransi c' },

        { key: 'id_ron_kode', val: 'RON' },
        { key: 'rencana_terbang', val: 'Rencana terbang' },
        { key: 'warna', val: 'warna' },
        { key: 'id_aircraft', val: 'aircraft' },
        { key: 'keterangan', val: 'keterangan' }
    ];

    width = '0px';

    //untuk derazona pilot
    updateDetailJadwalDetail(id_jadwal_pegawai: string, data: any) {
        console.log(data);
        if (data.extra_duty == true) {
            data.extra_duty = 'ya';
        } else {
            data.extra_duty = 'tidak'
        }
        this.absensiPilotJadwalDetailService.putAbsensiPilotJadwalDetailsId(
            {
                id: id_jadwal_pegawai,
                body: {
                    id_jadwal_detail: id_jadwal_pegawai,
                    id_ron_kode: data.id_ron_kode,
                    id_aircraft: data.id_aircraft,
                    rencana_terbang: data.rencana_terbang,
                    keterangan: data.keterangan,
                    extra_duty: data.extra_duty,
                    extra_duty_vanue: data.extra_duty_vanue,
                    warna: data.warna
                }
            }
        ).subscribe(
            data => {

            }
        )

        if (data.extra_duty == 'ya') {
            data.extra_duty = true;
        } else {
            data.extra_duty = false
        }
    }

    updateDetailJadwal(id_jadwal_pegawai: string, data: any) {
        if (data.status_jadwal == true) {
            data.status_jadwal = 'ya';
        } else {
            data.status_jadwal = 'tidak'
        }

        let param: Jadwal_pegawai_terjadwal = {
            jam_masuk: data.jam_masuk,
            jam_keluar: data.jam_keluar,
            lama_kerja: data.lama_kerja,
            toleransi_telat: data.toleransi_telat,
            toleransi_cabut: data.toleransi_cabut,
            lama_istirahat: data.lama_istirahat,
            status_jadwal: data.status_jadwal
        }

        this.spinner.show()
        this.absensiJadwalPegawaiTerjadwalService.putAbsensiJadwalPegawaiTerjadwalsId({
            id: id_jadwal_pegawai,
            body: param
        }).subscribe(
            data => {
                this.spinner.hide();

            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('gagal di update');
            }
        )
        if (data.status_jadwal == 'ya') {
            data.status_jadwal = true;
        } else {
            data.status_jadwal = false
        }




    }



}
