import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AbsensiRoutingModule } from './absensi-routing.module';
import { ListComponent } from './list/list.component';
import { NgZorroAntdModule, NzRadioModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DetailComponent } from './detail/detail.component';
import { VerifikasiComponent } from './verifikasi/verifikasi.component';


@NgModule({
    declarations: [ListComponent, DetailComponent, VerifikasiComponent],
    imports: [
        CommonModule,
        AbsensiRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        NzRadioModule,
    ],
    providers: [FormBuilderTypeSafe],
    entryComponents: [DetailComponent, VerifikasiComponent]
})
export class AbsensiModule { }
